// Helper function for getting text in current language
export const getText = (textObj, language = "en") => {
  if (typeof textObj === "string") return textObj;

  if (textObj && typeof textObj === "object") {
    return (
      textObj[language] ||
      textObj.en ||
      textObj.ko ||
      textObj.zh ||
      Object.values(textObj)[0] ||
      ""
    );
  }
  return textObj || "";
};

